<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="mantenimiento.meta_semana"
    :progress="mantenimiento.progress"
    :name="mantenimiento.nombre"
    :depto="mantenimiento.depto"
    :days="mantenimiento.dias_laborados"
    :m3="mantenimiento.m3_persona"
    :bonus_p="mantenimiento.bono_persona"
    :bonus_tot="mantenimiento.bono_depto"
    :asis="mantenimiento.asistencia"
    :extra="mantenimiento.$_extra_m3"
    :bonus_prod="mantenimiento.bono_productividad"
    :bonus_goals="mantenimiento.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Mantenimiento',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'mantenimiento',
            userCode: this.$route.params.user,
            city: 'nogales'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        mantenimiento() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>